.u-align-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-align-top {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-align-bottom {
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-font-enf {
  font-family: "Roboto Condensed", sans-serif; }

.u-reset-anchor {
  text-decoration: none;
  color: inherit;
  cursor: pointer; }
  .u-reset-anchor:visited, .u-reset-anchor:link {
    text-decoration: none;
    color: inherit; }

.u-checkbox-hide {
  display: none; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-size: 62.5%; }
  @media (min-width: 112.5em) {
    html {
      font-size: 80%; } }
  @media (max-width: 75em) {
    html {
      font-size: 70%; } }
  @media (max-width: 56.25em) {
    html {
      font-size: 60%; } }
  @media (max-width: 37.5em) {
    html {
      font-size: 50%; } }

body {
  font-family: "Lato", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

::selection {
  background-color: #039;
  color: #fff; }
