.u-align-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-align-top {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-align-bottom {
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -50%); }

.u-font-enf {
  font-family: "Roboto Condensed", sans-serif; }

.u-reset-anchor {
  text-decoration: none;
  color: inherit;
  cursor: pointer; }
  .u-reset-anchor:visited, .u-reset-anchor:link {
    text-decoration: none;
    color: inherit; }

.u-checkbox-hide {
  display: none; }

.Row {
  width: 100%;
  max-width: 114rem;
  margin: 0 auto; }
  .Row:not(:last-child) {
    margin-bottom: 0.5rem; }
  .Row::after {
    content: "";
    display: table;
    clear: both; }

.Col {
  box-sizing: border-box;
  position: relative;
  width: 100%;
  float: left; }
  .Col:not(:last-child) {
    margin-right: 1.5rem; }

.col-1-of-2 {
  width: calc((100% - 1.5rem) / 2); }

.col-1-of-3 {
  width: calc((100% - 2 * 1.5rem) / 3); }

.col-2-of-3 {
  width: calc( 2 * ((100% - 2 * 1.5rem) / 3) + 1.5rem); }

.col-1-of-4 {
  width: calc((100% - 3 * 1.5rem) / 4); }

.col-2-of-4 {
  width: calc( 2 * ((100% - 3 * 1.5rem) / 4) + 1.5rem); }

.col-3-of-4 {
  width: calc( 3 * ((100% - 3 * 1.5rem) / 4) + 2 * 1.5rem); }

.col-1-of-5 {
  width: calc((100% - 5 * 1.5rem) / 5); }

.col-2-of-5 {
  width: calc( 2 * ((100% - 5 * 1.5rem) / 5) + 1.5rem); }

.col-3-of-5 {
  width: calc( 3 * ((100% - 5 * 1.5rem) / 5) + 2 * 1.5rem); }

.col-4-of-5 {
  width: calc( 4 * ((100% - 5 * 1.5rem) / 5) + 3 * 1.5rem); }

.col-1-of-6 {
  width: calc((100% - 5 * 1.5rem) / 6); }

.col-2-of-6 {
  width: calc( 2 * ((100% - 5 * 1.5rem) / 6) + 1.5rem); }

.col-3-of-6 {
  width: calc( 3 * ((100% - 5 * 1.5rem) / 6) + 2 * 1.5rem); }

.col-4-of-6 {
  width: calc( 2 * ((100% - 5 * 1.5rem) / 6) + 3 * 1.5rem); }

.col-5-of-6 {
  width: calc( 2 * ((100% - 5 * 1.5rem) / 6) + 4 * 1.5rem); }

@media (max-width: 56.25em) {
  .col-1-of-6 {
    width: calc((100% - 2 * 1.5rem) / 3); }
    .col-1-of-6:not(:last-child) {
      margin-bottom: 10rem; }
  .col-2-of-6 {
    width: 100%; }
    .col-2-of-6:not(:last-child) {
      margin-bottom: 10rem; }
  .col-3-of-6 {
    width: calc( 2 * ((97% - 2 * 1.5rem) / 3) + 1.5rem); }
    .col-3-of-6:not(:last-child) {
      margin-bottom: 10rem; } }

@media (max-width: 37.5em) {
  .col-1-of-4 {
    width: calc((100% - 5 * 1.5rem) / 5); }
  .col-2-of-4 {
    transform: translateX(-2rem);
    width: calc( 3 * ((100% - 5 * 1.5rem) / 5) + 2 * 1.5rem); }
  .col-1-of-6 {
    width: 100%; }
    .col-1-of-6:not(:last-child) {
      margin-bottom: 10rem; }
  .col-2-of-6 {
    width: 100%; }
    .col-2-of-6:not(:last-child) {
      margin-bottom: 2rem; }
  .col-3-of-6 {
    width: 100%; }
    .col-3-of-6:not(:last-child) {
      margin-bottom: 2rem; } }

@media (max-width: 56.25em) {
  .header__item__left {
    display: none; }
  .header__item__right {
    width: 100%; } }
